.preloader-wrap {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  background: #fff;
  z-index: 2000;
  text-align: center;
  height: 100vh;
}
.preloader-wrap .inner {
  position: absolute;
  height: 170px;
  width: 170px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #5D78FF;
  border-top: 5px solid #5D78FF;
  border-radius: 50%;
  animation: spin 1s infinite linear;
}
@media (max-width: 991px) {
  .preloader-wrap .inner {
    height: 150px;
    width: 150px;
  }
}
.preloader-wrap .percentage {
  z-index: 100;
  color: #5D78FF;
  opacity: 1;
  font-weight: 600;
  font-family: "bebasbold";
  font-size: 60px;
  letter-spacing: 5px;
  line-height: 1;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
@media (max-width: 991px) {
  .preloader-wrap .percentage {
    font-size: 50px;
  }
}

@keyframes spin {
  0% {
    transform: translate(-50%, -50%) rotate(0);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
